import React from 'react'
import { connect } from 'react-redux'
import { getAllOrder } from '../../redux/actions'
import {
  BarChart,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  Legend,
  Bar
} from 'recharts'

class Graph extends React.Component {
  componentDidMount() {
    console.log('graph mount')
    this.props.getAllOrder(this.props.web3)
  }

  render() {
    return (
      <div className="container-fluid" style={{ marginTop: '100px' }}>
        <BarChart width={730} height={250} data={this.props.graphData}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="day" />
          <YAxis />
          <Tooltip />
          <Legend />
          <Bar dataKey="total" fill="#8884d8" />
        </BarChart>
      </div>
    )
  }
}

const mapStateToProps = ({ graph }) => {
  return {
    graphData: graph.volumn
  }
}

export default connect(mapStateToProps, { getAllOrder })(Graph)
